<script>
import github from "@/assets/img/github.svg";
import google from "@/assets/img/google.svg";
import {get,post} from '@/Helpers/api';

export default {
  data() {
    return {
      github,
      google,
      // fullname,
      // email,
      // department,
      // password
      form:{
        fullname :null,
        email : null,
        department: null,
        password : null
      },
      departments:[]
    };
  },
  methods:{
    registerUser(){
      console.log('my data', this.form)
      post('https://server.digitallyfreed.com/api/register', this.form)
      .then(res => {
        console.log('reg response', res.data.user)
        if(res.data.user){
          localStorage.setItem('userName', res.data.user.name)

          localStorage.setItem('department', res.data.user.department_id)
          localStorage.setItem('id', res.data.user.id)

          window.location.href= '/auth/login';
          // this.$router.push({ path : '/auth/login' });

        }

      })
      .catch(error => {
     console.log(error.message)
  })
    },
    getDepartments(){
      get('https://server.digitallyfreed.com/api/departments').then(res => {
        console.log('departments',  res.data.departments)
        this.departments = res.data.departments
      })
    }
  },
  created(){
    this.getDepartments()
  }
};
</script>
